<template>
    <div class="bglayout">
        <audio ref="bgsound" loop>
            <source src="/static/hajimi.mp3" type="audio/mp3"/>
        </audio>
    </div>
    <div class="fixedlogo" :class="soundmode?'fixedlogoon':'fixedlogooff'" @click="changesoundmode"></div>
    <div class="loginbg">
        <div class="loginpad">
            <div class="loginform" :class="ismobile?'widthifmobile':'widthifpc'">
                <div class="padtitle">{{ $langsmgr("langkey.sitename") }}</div>
                <div class="padsubtitle">{{ $langsmgr("langkey.sitesubtitle") }}</div>
                <div v-if="isenvchecked" class="loginarea">
                    <div class="loginarea-line">
                        <a-input ref="unameinput" v-model:value="unameval" 
                            :disabled="isnotcheck" :placeholder="$langsmgr('langkey.component.login.uname.placeholder')"
                            @pressEnter="login()">
                            <template #prefix>
                                <MailOutlined/>
                            </template>
                        </a-input>
                    </div>
                    <div class="loginarea-line">
                        
                    </div>
                    <div class="loginarea-line">
                        <a-input-password ref="upasswdinput" v-model:value="upasswdval" 
                            :disabled="isnotcheck" :placeholder="$langsmgr('langkey.component.login.passwd.placeholder')"
                            @pressEnter="login()">
                            <template #prefix>
                                <edit-outlined/>
                            </template>
                        </a-input-password>
                    </div>
                    <!-- <div v-if="false" class="loginarea-line">
                        <span class="recaptchacheck">
                            <CheckOutlined v-if="!isnotcheck" class="checkoutlined"/>
                            <a-button :type="recaptchatype" size="small" :loading="isrecaptchachecking" :disabled="!isnotcheck" @click="recaptcha">{{recaptchatext}}</a-button>
                        </span>
                    </div> -->
                    <div class="loginarea-line">
                        <span class="remeline">
                            <a-checkbox class="remeckbox"> {{$langsmgr('langkey.component.login.remeberme')}}</a-checkbox>
                        </span>
                    </div>
                    <div class="clearfix"></div>
                    <div class="loginarea-line loginbtnline">
                        <a-button type="primary" :block="true" size="normal" :class="'fakebtn'" :loading="isloginning" :disabled="isnotcheck" 
                            @click="login()">
                            <LoginOutlined/>
                            {{logintext}}
                        </a-button>
                    </div>
                </div>
                <div v-else class="loading">
                    <LoadingOutlined/>
                    <a class="loading-text">{{$langsmgr('langkey.component.login.preloading')}}</a>
                </div>
                <CPRArea type="signin"/>
            </div>
        </div>
    </div>
</template>
<script setup>
import { LoadingOutlined,UserOutlined,EditOutlined,CheckOutlined,MailOutlined,LoginOutlined } from '@ant-design/icons-vue'
//import { useReCaptcha } from 'vue-recaptcha-v3'
import { getCurrentInstance, ref, nextTick, onMounted } from 'vue'
import axios from 'axios'
import { notification,message } from 'ant-design-vue'
import CPRArea from './cprarea.vue'

const gconfig = getCurrentInstance().appContext.config.globalProperties;

const ismobile = ref(gconfig.$ismobile())

const unameinput = ref(null)
const upasswdinput = ref(null)
const unameval = ref('')
const upasswdval = ref('')


const state = ref(false)
const isenvchecked = ref(false)
const isnotcheck = ref(false)
const isloginning = ref(false)
const isrecaptchachecking = ref(false)
const recaptchatype = ref('primary')
const logintext = ref(null)
const recaptchatext = ref(null)
const gtoken = ref(null)

const bgsound = ref()
const soundmode = ref(gconfig.$getsoundmode())
const changesoundmode = () => {
    gconfig.$setsoundmode(soundmode.value?false:true)
    soundmode.value = gconfig.$getsoundmode()
    if(soundmode.value) bgsound.value.play()
    else bgsound.value.pause()
}

const winsize = ref({
    width: null,
    height: null
})
const whenWindowResize = ()=>{
    winsize.value.width = window.innerWidth
    winsize.value.height = window.innerWidth
}

var __temp_sign = ""

//const {executeRecaptcha, recaptchaLoaded} = useReCaptcha()

logintext.value = gconfig.$langsmgr("langkey.component.login.submit.normal")
recaptchatext.value = gconfig.$langsmgr("langkey.component.login.recaptcha.normal")



