<style src="./../assets/common.css" scoped></style>
<style scoped>
.bglayout{
    position:fixed;
    top:0;
    left: 0;
    width:100%;
    height:100%;
    background-color:#f0e8e5;
}
.fixedlogo{
    cursor: pointer;
    position:fixed;
    width:250px;
    height: 250px;
    right: 10px;
    bottom: 10px;
    background-size: 250px 250px;
    background-repeat: no-repeat;
    z-index: 999;
}
.fixedlogoon{
    background-image: url('../assets/hajimi.png');
}
.fixedlogooff{
    background-image: url('../assets/hajimi-nosound.png');
}
#bgvdo{
    position:fixed;
    top:-50px;
    left:0;
    width:auto;
    height:auto;
    z-index:0;
    object-fit: fill;
}
.loginbtnline{
    /* padding-top:20px; */
}
.remeline{
    float: left;
}
.remeckbox{
    color:#999;
}
</style>